import { previewImg } from '../commons/js/common.js';
export default {
	name: 'swiper',
	props: {
		banner: Array,
		nav: String,
		title: String,
		leaveword:Array
	},
	data() {
		return {
			bannerIndex: 1
		};
	},
	watch: {
		banner(newValue, oldValue) {
			return newValue.slice(0, 3);
		}
	},
	methods: {
		// 轮播图预览
		localCont(id) {
			previewImg(this.banner,id)
		},
		// 监听轮播图左右滑动事件
		bannerFun(e) {
			this.bannerIndex = e.detail.current + 1;
		}
	}
};
